$eigengrau: #16161D;
$postit: #ffffbb;

:root {
  padding: 1em;
  background-color: $eigengrau;
  text-align: justify

}

h1, h2 {
  color: white;
  font-style: italic;
}

.note {
  padding: 2em;
  margin-bottom: 2em;
  font-size: 15px;
  background-color: $postit;
  border-style: solid;
  border-width: 1px;
  border-radius: 0.1em;
}

button {
  min-width: 10em;
  background-color: $eigengrau;
  color: white;
}

p, td {
  margin-top: 1em;
  color: white
}

.error {
    color: red;
    background: lightgrey;
    font-size: 20px;
    border-style: solid;
    border-radius: 5px;
    padding: 10px;
    margin-bottom: 10px;
}

.update {
    color: darkgreen;
    background: lightgrey;
    font-size: 20px;
    border-style: solid;
    border-radius: 5px;
    padding: 10px;
    margin-bottom: 10px;
}